import React, { useState } from 'react';
import axios from 'axios';

const Login = () => {
    const [login, setLogin] = useState("")
    const [pass, setPass] = useState("")
    const [help, setHelp] = useState(false);
    const handleHidePass = (e) => {
        const type = document.getElementById("password")
        if (type.getAttribute("type") === "password") {
            type.setAttribute("type", "text")
            e.target.classList.add("fa-eye")
            e.target.classList.remove("fa-eye-slash")
        } else {
            type.setAttribute("type", "password")
            e.target.classList.remove("fa-eye")
            e.target.classList.add("fa-eye-slash")
        }
    }
    const handleLogin = (e) => {
        setLogin(e.target.value)
    }
    const checkLogin = (e) => {
        if (e.target.value === "" || e.target.value.length < 3) {
            e.target.style.border = "1px solid #fe475d"
        } else {
            e.target.style.border = "1px solid #22a77d"
        }
    }
    const handlePass = (e) => {
        setPass(e.target.value)
    }
    const checkPass = (e) => {
        if (e.target.value === "" || e.target.value.length < 6) {
            e.target.style.border = "1px solid #fe475d"
        } else {
            e.target.style.border = "1px solid #22a77d"
        }

    }
    const handelSubmit = (e) => {
        e.preventDefault()
        const formData = {
            user_login: login,
            user_pass: pass,
        }
        axios.post(`${process.env.REACT_APP_API_URL}api/users/login`, formData)
            .then(data => {
                console.log(data);
                if (data.status === 200) {
                    localStorage.setItem("admin_hotel", 'connected')
                    localStorage.setItem("userId", `${data.data.userId}`)
                    localStorage.setItem("token", `${data.data.token}`)
                    window.location.assign('/')
                } else {
                    if (data.data.message === "mauvais login") {
                        document.querySelector(".lable_login p").innerHTML = data.data.message
                    } else {
                        document.querySelector(".lable_login p").innerHTML = ""
                    }
                    if (data.data.message === "Mot de passe incorrect !") {
                        document.querySelector(".lable_pass p").innerHTML = data.data.message
                    } else {
                        document.querySelector(".lable_pass p").innerHTML = ""
                    }
                }
            })
            .catch(error => {
                console.warn(error)
            })
    }
    const hendleHelp = (e) => {
        e.preventDefault()
        setHelp(true)
    }
    return (
        <>
            <div className='login'>
                <div className="background" style={{ backgroundImage: `url('${process.env.PUBLIC_URL}images/pexels-pixabay-434337.jpg')` }}></div>
                {/* <div className="video">
                    <video
                        className='background'
                        autoPlay={'autoplay'}
                        loop
                    >
                        <source src={`${process.env.PUBLIC_URL}images/neon-stocks-background.mp4`} />
                    </video>
                </div> */}
                <form action="" onSubmit={handelSubmit}>
                    {help ? (
                        <div className="help">
                            <h3>assistance</h3>
                            <textarea
                                name="message"
                                placeholder="décrivez votre poblème a l'equipe de developement"
                            />
                            <button type='button'>
                                envoyer
                                <i className='far fa-paper-plane'></i>
                            </button>
                            <button className='close' onClick={(e) => setHelp(false)}>
                                <i className='fas fa-times'></i>
                            </button>
                        </div>

                    ) : (
                        <>
                            <img src="./images/profil1.jpg" alt="login" />
                            <label className='lable_login' htmlFor="login">
                                identifiant
                                <input
                                    value={login}
                                    onChange={handleLogin}
                                    onBlur={checkLogin}
                                    className='form_control'
                                    type="text"
                                    autoFocus
                                    name='user_login'
                                    placeholder='login'
                                    id='inputLogin'
                                />
                                <i className='far fa-user-circle'></i>
                                <p></p>
                            </label>
                            <label className='lable_pass' htmlFor="password">
                                mot de passe
                                <input
                                    value={pass}
                                    onChange={handlePass}
                                    onBlur={checkPass}
                                    className='form_control'
                                    type="password"
                                    name='user_pass'
                                    id='password'
                                    placeholder='password'
                                />
                                <i onClick={handleHidePass} className='far fa-eye-slash'></i>
                                <p></p>
                            </label>
                            <button className='btn' type='submit'>
                                se connecter
                            </button>
                            <a href="help" onClick={hendleHelp}>besoin d'aide ?</a>
                        </>
                    )
                    }
                </form>
            </div>
        </>
    );
};

export default Login;