import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Champ from './recettes/champ';
import Total from './recettes/total';
import moment from 'moment';

const TabRecettes = ({ departement, periode, rapport, children, selected, setSelected }) => {
    const [isAdding, setIsAdding] = useState(false);
    const [departementState, setDepartementState] = useState(departement);
    const [vercement, setVercement] = useState(false);
    const [credit, setCredit] = useState(false);
    const [recouvrement, setRecouvrement] = useState(false);
    const [observation, setObservation] = useState("");
    const [full, setFull] = useState(false)
    useEffect(() => {
        setDepartementState(departement);
    }, [departement])

    const handleAdd = () => {
        setIsAdding(false)
        axios.get(`${process.env.REACT_APP_API_URL}api/recettes/last/${departementState}`)
            .then(data => {
                let thisMonth = moment(data.data.date).format('DD') === "15" ? data.data.mois += 1 : data.data.mois
                const formData = {
                    vercement: vercement,
                    credit: credit,
                    recouvrement: recouvrement,
                    observation: observation,
                    departement: departementState,
                    date: moment(data.data.date).add(1, 'day').format('YYYY-MM-DD'),
                    mois: thisMonth,
                }
                axios.post(`${process.env.REACT_APP_API_URL}api/recettes`, formData)
                    .then(data => {
                        setVercement('');
                        setCredit('');
                        setRecouvrement('');
                        setObservation('');
                        getChamp()
                        setIsAdding(false);
                    }).catch(err => console.warn(err))
            }).catch(err => console.error(err))

    }

    const [datas, setDatas] = useState([]);
    useEffect(() => {
        getChamp()
        // eslint-disable-next-line
    }, [departementState, selected]);
    const getChamp = () => {
        axios.get(`${process.env.REACT_APP_API_URL}api/recettes/mois/${selected}`)
            .then(data => {
                const result = data.data.filter(depart => depart.departement === departementState);
                setDatas(result);
                if (data.data.length === 0) setFull(false)
                else moment(result[result.length - 1].date).format('DD') === "15" ? setFull(true) : setFull(false)
            }).catch(err => console.log(err))
    }

    return (
        <div className="tab_recettes">
            {children[0]}
            <h3>etat de recette et credit</h3>
            <h4>{periode}</h4>
            {departement !== undefined ? <h4>departement : <span>{departement}</span></h4> : ""}
            {/* {!rapport &&
                <button className='print' >
                    print <i className="fas fa-print"></i>
                    <span className="tooltip">imprimer le document</span>
                </button>
            } */}
            <table className="table">
                <thead>
                    <tr>
                        <th className='date'>date <i className='far fa-calendar-alt'></i></th>
                        <th>versements</th>
                        <th className='credit'>credits</th>
                        <th>recouvrements</th>
                        <th>Observations</th>
                        <th className='tr_action'><i className='fas fa-cog'></i></th>
                    </tr>
                </thead>
                <tbody>
                    {datas.map((data, key) =>
                        <Champ data={data} key={key} getChamp={getChamp} />
                    )}
                    <tr className='input_add'>
                        {isAdding && (
                            <>
                                <td data-label="date">date...</td>
                                <td data-label="vercement">
                                    <input
                                        type="number"
                                        onChange={(e) => setVercement(e.target.value)}
                                        value={vercement}
                                        placeholder='vercement'
                                    />
                                </td>
                                <td data-label="credit">
                                    <input
                                        type="number"
                                        onChange={(e) => setCredit(e.target.value)}
                                        value={credit}
                                        placeholder='credit'
                                    />
                                </td>
                                <td data-label="recouvrement">
                                    <input
                                        type="number"
                                        onChange={(e) => setRecouvrement(e.target.value)}
                                        value={recouvrement}
                                        placeholder='recouvrement'
                                    />
                                </td>
                                <td data-label="observation">
                                    <input
                                        type="text"
                                        onChange={(e) => setObservation(e.target.value)}
                                        value={observation}
                                        placeholder='observation'
                                    />
                                </td>

                                <td
                                    className='action submit'
                                    onClick={handleAdd}
                                    data-label="action"
                                >
                                    <i className='fas fa-check'></i>
                                </td>
                            </>
                        )}
                    </tr>
                    <Total datas={datas} />
                </tbody>
            </table>
            {!rapport &&
                !full ? (
                <button
                    style={isAdding ? { background: "var(--red)" } : {}}
                    onClick={() => isAdding ? setIsAdding(false) : setIsAdding(true)}
                >
                    {isAdding ?
                        <i className="fas fa-times"></i>
                        :
                        <i className="fas fa-plus"></i>
                    }
                    <span className="tooltip">{isAdding ? "annuler" : "ajouter un etat"}</span>
                </button>
            ) : (
                <button
                    style={{ background: "gray" }}
                    onClick={() => setSelected(selected + 1)}
                >
                    <i className="fas fa-chevron-right"></i>
                    <span className="tooltip">suivant</span>
                </button>
            )
            }
            {children[1]}
        </div>
    );
};

export default TabRecettes;