import React, { useState } from 'react';
import { Route, Switch } from "react-router-dom";
import Home from "./Pages/home";
import Analytics from "./Pages/analytics";
import Notifications from "./Pages/notifications";
import Depenses from './Pages/depenses';
import Recettes from './Pages/recettes';
import Helps from './Pages/helps';
import Settings from './Pages/settings';
import NotFound from './Pages/notFound';
import EmployeesPage from './Pages/employees.page';
import Rapport from './Pages/rapport';

const Routeur = () => {
    const [selected, setSelected] = useState(1);
    return (
        <Switch>
            <Route exact path="/">
                <Home selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/home">
                <Home selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/analytics">
                <Analytics selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/notifications" component={Notifications}></Route>

            <Route exact path="/recettes" >
                <Recettes selected={selected} setSelected={setSelected} />
            </Route>
            <Route path="/recettes/:depart" >
                <Recettes selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/depenses" >
                <Depenses selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/helps" component={Helps}></Route>

            <Route path="/rapport">
                <Rapport print={false} selected={selected} setSelected={setSelected} />
            </Route>

            <Route path="/settings" component={Settings}></Route>
            <Route path="/employees" component={EmployeesPage}></Route>
            <Route component={NotFound}></Route>
        </Switch>
    );
};

export default Routeur;