import React, { useState } from 'react';
import TabRecettes from '../Component/tabRecettes';
import TitleBar from '../Component/titleBar';

const Recettes = ({ selected, setSelected, match }) => {
	const [depart, setDepart] = useState("bar")
	// console.log(match.params.depart); 	
	let periode = "";
	switch (selected) {
		case 1: periode = "du 16 fevrier au 15 mars"; break;
		case 2: periode = "du 16 mars au 15 avril"; break;
		case 3: periode = "du 16 avril au 15 mai"; break;
		case 4: periode = "du 16 mai au 15 juin"; break;
		case 5: periode = "du 16 juin au 15 juillet"; break;
		case 6: periode = "du 16 juillet au 15 aougt"; break;
		case 7: periode = "du 16 aougt au 15 septembre"; break;
		case 8: periode = "du 16 septembre au 15 octobre"; break;
		case 9: periode = "du 16 octobre au 15 novembre"; break;
		case 10: periode = "du 16 novembre au 15 descembre"; break;
		case 11: periode = "du 16 descembre au 15 janvier"; break;
		case 12: periode = "du 16 janvier au 15 fevrier"; break;
		default:
			periode = "période invalide !"
			break;
	}
	return (
		<div className="recettes">
			<TitleBar
				selected={selected}
				setSelected={setSelected}
				title="les recettes"
			/>
			<nav className="tab_recette">
				<div className="onglets">
					<button
						className={depart === "bar" ? "active" : ""}
						onClick={() => setDepart("bar")}
					>bar</button>
					<button
						className={depart === "hebergement" ? "active" : ""}
						onClick={() => setDepart("hebergement")}
					>hebergement</button>
					<button
						className={depart === "restaurant" ? "active" : ""}
						onClick={() => setDepart("restaurant")}
					>restaurant</button>
				</div>
				{depart === "bar" &&
					<TabRecettes
						selected={selected}
						setSelected={setSelected}
						rapport={false}
						departement="bar"
						periode={periode}
						children={""}
					/>
				}
				{depart === "hebergement" &&
					<TabRecettes
						selected={selected}
						setSelected={setSelected}
						rapport={false}
						departement="hebergement"
						periode={periode}
						children={""}
					/>
				}
				{depart === "restaurant" &&
					<TabRecettes
						selected={selected}
						setSelected={setSelected}
						rapport={false}
						departement="restaurant"
						periode={periode}
						children={""}
					/>
				}
			</nav>
		</div >
	);
};

export default Recettes;