import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Champ from './depenses/champ';
import Total from './depenses/total';
import moment from 'moment';

const TabDepenses = ({ periode, children, rapport, selected, setSelected }) => {
    const [isAdding, setIsAdding] = useState(false);
    const [bar, setBar] = useState(false);
    const [cuisine, setCuisine] = useState(false);
    const [etage, setEtage] = useState(false);
    const [Transp, setTransp] = useState(false);
    const [Factures, setFactures] = useState(false);
    const [Maintenance, setMaintenance] = useState(false);
    const [Salaires, setSalaires] = useState(false);
    const [Autres, setAutres] = useState(false);
    const [datas, setDatas] = useState([]);
    const [full, setFull] = useState(false);

    const handleAdd = () => {
        setIsAdding(false)
        axios.get(`${process.env.REACT_APP_API_URL}api/depenses/last`)
            .then(data => {
                let thisMonth = moment(data.data.date).format('DD') === "15" ? data.data.mois += 1 : data.data.mois
                const formData = {
                    bar: bar,
                    hebergement: etage,
                    restaurant: cuisine,
                    tcb: Transp,
                    facture: Factures,
                    maintenence: Maintenance,
                    autres: Autres,
                    salaire: Salaires,
                    date: moment(data.data.date).add(1, 'day').format('YYYY-MM-DD'),
                    mois: thisMonth,
                }
                axios.post(`${process.env.REACT_APP_API_URL}api/depenses`, formData)
                    .then(data => {
                        setBar('');
                        setCuisine('');
                        setEtage('');
                        setTransp('');
                        setFactures('');
                        setMaintenance('');
                        setSalaires('');
                        setAutres('');
                        getChamp()
                        setIsAdding(false);
                    }).catch(err => console.warn(err))
            }).catch(err => console.log(err))

    }
    useEffect(() => {
        getChamp()
        // eslint-disable-next-line
    }, [selected]);
    const getChamp = () => {
        axios.get(`${process.env.REACT_APP_API_URL}api/depenses/mois/${selected}`)
            .then(data => {
                setDatas(data.data);
                if (data.data.length === 0) setFull(false)
                else moment(data.data[data.data.length - 1].date).format('DD') === "15" ? setFull(true) : setFull(false)
            }).catch(err => console.log(err))
    }
    return (
        <div className="tab_depenses">
            {children[0]}
            <h3>ETATS DES DEPENSE</h3>
            <span>{periode}</span>
            <table className="table">
                <thead>
                    <tr>
                        <th className='date'>
                            date
                            <i className='far fa-calendar-alt'></i>
                        </th>
                        <th>bar</th>
                        <th>cuisine</th>
                        <th>etage</th>
                        <th>Transp Crédit Bureautiq</th>
                        <th>Factures</th>
                        <th>Maintenance</th>
                        <th>Salaires</th>
                        <th>Autres Dépenses</th>
                        <th className='tr_action'><i className='fas fa-cog'></i></th>
                    </tr>
                </thead>
                <tbody>
                    {datas.map((data, key) =>
                        <Champ data={data} key={key} getChamp={getChamp} />
                    )}
                    <tr className='input_add'>
                        {isAdding && (
                            <>
                                <td data-label="date">date...</td>
                                <td data-label="bar">
                                    <input
                                        type="number"
                                        onChange={(e) => setBar(e.target.value)}
                                        value={bar}
                                        placeholder='bar'
                                    />
                                </td>
                                <td data-label="cuisine">
                                    <input
                                        type="number"
                                        onChange={(e) => setCuisine(e.target.value)}
                                        value={cuisine}
                                        placeholder='cuisine'
                                    />
                                </td>
                                <td data-label="etage">
                                    <input
                                        type="number"
                                        onChange={(e) => setEtage(e.target.value)}
                                        value={etage}
                                        placeholder='etage'
                                    />
                                </td>
                                <td data-label="Transp/Crédit">
                                    <input
                                        type="number"
                                        onChange={(e) => setTransp(e.target.value)}
                                        value={Transp}
                                        placeholder='Transp'
                                    />
                                </td>
                                <td data-label="Factures">
                                    <input
                                        type="number"
                                        onChange={(e) => setFactures(e.target.value)}
                                        value={Factures}
                                        placeholder='Factures'
                                    />
                                </td>
                                <td data-label="Maintenance">
                                    <input
                                        type="number"
                                        onChange={(e) => setMaintenance(e.target.value)}
                                        value={Maintenance}
                                        placeholder='Maintenance'
                                    />
                                </td>
                                <td data-label="Salaires">
                                    <input
                                        type="number"
                                        onChange={(e) => setSalaires(e.target.value)}
                                        value={Salaires}
                                        placeholder='Salaires'
                                    />
                                </td>
                                <td data-label="Autres">
                                    <input
                                        type="number"
                                        onChange={(e) => setAutres(e.target.value)}
                                        value={Autres}
                                        placeholder='Autres'
                                    />
                                </td>
                                <td
                                    className='action submit'
                                    onClick={handleAdd}
                                    data-label="action">
                                    <i className='fas fa-check'></i>
                                </td>
                            </>
                        )}
                    </tr>
                    <Total datas={datas} />
                </tbody>
            </table>
            {!rapport &&
                !full ? (
                <button
                    style={isAdding ? { background: "var(--red)" } : {}}
                    onClick={() => isAdding ? setIsAdding(false) : setIsAdding(true)}
                >
                    {isAdding ?
                        <i className="fas fa-times"></i>
                        :
                        <i className="fas fa-plus"></i>
                    }
                    <span className="tooltip">{isAdding ? "annuler" : "ajouter un etat"}</span>
                </button>
            ) : (
                <button
                    style={{ background: "gray" }}
                    onClick={() => setSelected(selected + 1)}
                >
                    <i className="fas fa-chevron-right"></i>
                    <span className="tooltip">suivant</span>
                </button>
            )
            }
            {children[1]}
        </div>
    );
};


export default TabDepenses;